.nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;    
    max-width: 1100px;
    margin: auto;
    box-sizing: border-box;
    min-height: 90px;
    border-bottom: 0;
    }
    .container{
    border-bottom: 1px solid rgb(219, 219, 219);  
    }
    ul{
    list-style: none;   
    display: flex;
    flex-direction: row; 
    padding-left: 0;
    }
    li{
    margin-right: 15px;    
    }
    a{
        text-decoration: none;
    color: #0693e3;
    }
    a:hover{
    color: rgba(0, 0, 0, 0.76);
    }
    img{
    margin-left: 4px;}
    @media (max-width: 500px) {
    img{
    width: 100px;        }
    li{
    font-size: 12px;    }
    ul{margin-left: 10px;}
    
      }
    @media (max-width: 800px) {
    nav{
    min-height: 60px;        }   
    }
    